  font-weight: 400;
  margin: 20px 0;
}
.page-body h2{
  font-size: 1.5rem;
  font-weight: 400;
}
.box{
  background: #fff;
  border-radius: 3px;
  padding: 15px;
}
.box .box-header:before,
.box .box-header:after,
.box .box-footer:before,
.box .box-footer:after{
  content: "";
  display: table;
  clear: both;
}
.box .box-header{
  padding-bottom: 15px;
}
.box .box-footer{
  padding: 15px 0;
}
.box .box-header .box-title{
  font-size: 18px;
  font-weight: 100;
  margin: 0;
}
