  margin: 0 auto;
  width: 60%;
  height: 4px;
  background: rgba(245,245,245, 0.2);
  overflow: hidden;
}
.progress-bg .progress{
  width: 50%;
  height: 4px;
  margin-left: -50%;
  background: #19a05e;
  float: left;
  -webkit-animation: progress 1s linear infinite;
          animation: progress 1s linear infinite;
}
@-webkit-keyframes progress {
  0% {
    margin-left: -50%;
  }
  100% {
    margin-left: 100%;
  }
}
@keyframes progress {
  0% {
    margin-left: -50%;
  }
  100% {
    margin-left: 100%;
  }
}
